<template>
  <div class="manage">
    <el-dialog
        :title="modelType===0 ?'新增用户':'编辑用户'"
        :visible.sync="dialogFormVisible"
        :before-close="handleClose"
        width="50%">
      <el-form ref="form" :model="form" :inline="true" :rules="rules" label-width="80px" :show-message="true" :before-close="handleClose">
        <el-form-item label="姓名" prop="name">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="年龄" prop="age">
          <el-input placeholder="请输入年龄" v-model="form.age" type="number"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="sex">
          <el-select v-model="form.sex" placeholder="选择性别">
            <el-option label="男" :value="0"></el-option>
            <el-option label="女" :value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item  label="出生日期" prop="birth">
            <el-date-picker
                v-model="form.birth"
                type="date"
                placeholder="选择日期"
            value-format="yyyy-MM-DD">
            </el-date-picker>
        </el-form-item>
        <el-form-item label="地址" prop="addr">
          <el-input v-model="form.addr" placeholder="请输入地址"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleClose">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>
    <div class="manage-header">
      <el-button type="primary" @click="handleAdd">+ 新增</el-button>
      <el-form :inline="true">
        <el-form-item :model="userSearchForm">
          <el-input placeholder="请输入名称" v-model="userSearchForm.name"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button @click="handleSearch" type="primary">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="mange-content">
      <el-table
          :data="tableData"
          border
          stripe
          style="width: 100%"
          height="500px">
        <el-table-column
            prop="name"
            label="姓名"
            width="180">
        </el-table-column>
        <el-table-column
            prop="age"
            label="年龄"
            width="180">
        </el-table-column>
        <el-table-column
            prop="sex"
            label="性别"
            width="180">
          <template v-slot:default="scope">
            <span>{{ scope.row.sex === 0 ? '男' : '女'}}</span>
          </template>
        </el-table-column>
        <el-table-column
            prop="birth"
            label="出生日期">
        </el-table-column>
        <el-table-column
            prop="addr"
            label="地址">
        </el-table-column>
        <el-table-column
            label="操作">
          <template v-slot="scope">
            <el-button @click="handleEdit(scope.row)" size="mini">编辑</el-button>
            <el-button type="danger" @click="handleDelete(scope.row)" size="mini">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="mange-footer">
      <el-pagination
          @current-change="handlePage"
          background
          layout="prev, pager, next"
          :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import {getUser,addUser,editUser,delUser} from '@/api'
export default {
  name:'myUser',
  data() {
    return {
      tableData: [],
      dialogFormVisible: false,
      modelType:0,
      total:0,
      pageData:{
        page:1,
        limit:10
      },
      userSearchForm:{
        name:''
      },
      form: {
        name: '',
        age:'',
        sex: '',
        birth: '',
        addr: '',
      },
      rules: {
        name: [
          { required: true, message: '请输入姓名', trigger: 'blur' },
          { min: 2, max: 4, message: '长度在 2 到 4 个字符', trigger: 'blur' }
        ],
        age: [
          { required: true, message: '年龄不能为空',trigger: 'blur'},
        ],
        sex:[{ required: true, message: '请选择性别', trigger: 'blur' }],
        birth:[{ required: true, message: '请选择出生日期', trigger: 'blur' }],
        addr:[{ required: true, message: '请输入地址', trigger: 'blur' }],
      }
    };
  },
  methods: {
    handleClose() {
      /*
      这里初始值指的是初次给model赋的值--(<el-form  :model="form">)
      重置表单数据，但是只是回到初始值，
      就该项目来说，当用户第一次点击新增或者编辑，也就是当用户打开了表单页面时
      就是初次渲染，那么form的初始值将永远为本次渲染的值
              1.如果用户首次点击了编辑，
      那么初始值将为编辑的用户的数据（因为我们写了当点击编辑之后this.form=xxx）
              2.如果首次点击了新增，
      那么初始值为空（这个才是我们想要的结果）

      不管先点击哪个，只要每次取消或者确定都会调用---this.$refs.form.resetFields()
      所以初始值将永远都是用户第一次打开表单后给:model渲染的数据

              解决办法，(因为页面有初始数据，所以我们采用第二种方法)
       要么强制让初次渲染的model为空，
       要么每次取消都直接让form的每个属性为空，而不是使用重置方法
       */
      // this.$refs.form.resetFields()
      // 用下面这种方法能够彻底让form表单数据重置为空的状态
      const keys=Object.keys(this.form)
      keys.forEach(item=>this.form[item]='')
      this.dialogFormVisible=false
    },
    submit(){
      this.$refs.form.validate((valid) => {
        // 表单验证
        if(valid){
          if(this.modelType===0){
            addUser(this.form).then(()=>{
              this.getList()
            })
          }else{
            editUser(this.form).then(()=>{
              this.getList()
            })
          }
          this.handleClose()
        }
      })
    },
    handleAdd(){
      this.modelType=0
      this.dialogFormVisible=true
    },
    handleEdit(row){
      this.modelType=1
      //this.form=row //对象赋值默认为浅拷贝，拷贝的是对象的地址值,那么意味着现在两个对象将指向同一个地址值，也就是同一个对象
      this.form=JSON.parse(JSON.stringify(row))//对象深拷贝，拷贝的是对象的内容
      this.dialogFormVisible=true
    },
    handleDelete(row){
      this.$confirm('此操作将删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        delUser({ id:row.id }).then(()=>{
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          // 重新获取列表的接口
          this.getList()
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    getList(){
      //获取列表的数据
      getUser({params:{...this.pageData,...this.userSearchForm}}).then(({ data }) => {
        this.total=data.count || 0
        this.tableData=data.list
      })
    },
    handlePage(current){
      this.pageData.page=current
      this.getList()
    },
    handleSearch(){
      this.getList()
    }
  },
  created() {
    this.form=JSON.parse(JSON.stringify(this.form))
  },
  mounted() {
    this.getList()
  }
}
</script>

<style lang="less" scoped>
.manage-header{
  margin: 30px 0 10px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.mange-footer{
  position: absolute;
  right: 40px;
}
</style>